let uid = 0;
// 
class Dep {
    constructor() {
        // id,WatcherWatcher
        this.id = uid++;
        // 
        this.subs = [];
    }
    // targetWatcheraddDep,dep
    depend() {
        Dep.target.addDep(this);
    }
    // 
    addSub(sub) {
        this.subs.push(sub);
    }
    notify() {
        // (Watcher)
        this.subs.forEach(sub => sub.update());
    }
}
class Observer {
    constructor(value) {
        this.value = value;
        this.walk(value);
    }
    // 
    walk(value) {
        Object.keys(value).forEach(key => this.convert(key, value[key]));
    }
    // 
    convert(key, val) {
        defineReactive(this.value, key, val);
    }
}

function defineReactive(obj, key, val) {
    const dep = new Dep();
    // 
    let chlidOb = observe(val);
    Object.defineProperty(obj, key, {
        enumerable: true,
        configurable: true,
        get: () => {
            // Deptargetdepsubs
            // targetWatcherWatcher
            // Watcherdataget
            if (Dep.target) {
                dep.depend();
            }
            return val;
        },
        set: newVal => {
            if (val === newVal) return;
            val = newVal;
            // 
            chlidOb = observe(newVal);
            // 
            dep.notify();
        },
    });
}

function observe(value) {
    // 
    if (!value || typeof value !== 'object') {
        return;
    }
    return new Observer(value);
}

class Watcher {
    constructor(vm, expOrFn, cb) {
        this.depIds = {}; // hashid,
        this.vm = vm; // Vue
        this.cb = cb; // 
        this.expOrFn = expOrFn; // 
        this.val = this.get(); // 
    }
    // (Dep)
    update() {
        this.run();
    }
    addDep(dep) {
        // depIdshashid,Watcher,dep
        // idWatcher
        if (!this.depIds.hasOwnProperty(dep.id)) {
            dep.addSub(this);
            this.depIds[dep.id] = dep;
        }
    }
    run() {
        // console.log('run')
        const val = this.get();
        // console.log(val);
        if (val !== this.val) {
            let old = this.val;
            this.val = val;
            this.cb.call(this.vm, val, old);
        }
    }
    get() {
        // console.log('get')
        // (Watcher)
        Dep.target = this;
        const val = this.vm._data[this.expOrFn];
        // Watcher
        Dep.target = null;
        return val;
    }
}


class Vue {
    constructor(options = {}) {
        // $options
        this.$options = options;
        // data
        let data = (this._data = this.$options.data);
        // dataVue
        Object.keys(data).forEach(key => this._proxy(key));
        // 
        observe(data);
    }
    // 
    $watch(expOrFn, cb) {
        new Watcher(this, expOrFn, cb);
    }
    _proxy(key) {
        Object.defineProperty(this, key, {
            configurable: true,
            enumerable: true,
            get: () => this._data[key],
            set: val => {
                // console.log('set')
                this._data[key] = val;
            },
        });
    }
}

export default (data, cb) => {

    let my = {
        data: data
    }

    const myVue = new Vue(my);

    Object.keys(data).forEach(key => {
        myVue.$watch(key, (a, b) => {
            cb && cb(key, a, b)
        })
    });



    return my.data;
};


